import {Inject, Component, TRANSLATIONS} from '@angular/core';
import {FormControl} from '@angular/forms';
import {DataService} from '../services/data.service';
import {AppointmentUserTaskModel, AbstractDictModel, AppointmentModel, ClientModel, TaskModel} from '../models';
import {getLocaleDateTimeFormat} from '../../../node_modules/@angular/common';


@Component({
  selector: 'app-create-meeting',
  templateUrl: './create-meeting.component.html',
  styleUrls: ['./create-meeting.component.scss']
})

export class CreateMeetingComponent {

  appointment: AppointmentModel;
  appointmentUserTask: AppointmentUserTaskModel;
  tasks: TaskModel[];
  selectedItem = '';
  stateCtrl: FormControl;
  specialityCtrl: FormControl;
  clients: ClientModel[];

  constructor(private _data: DataService) {
    this.stateCtrl = new FormControl();
    this.specialityCtrl = new FormControl();
  }

  decrement(service) {
    if (service.count > 1) {
      service.count--;
    }
  }

  increment(service) {
    service.count++;
  }

  ngOnInit() {
    console.log('init');
    this.appointment = this._data.getCurrentAppointment();
    this.tasks = this._data.tasks;
    this.clients = this._data.clients;
  }

  AddService(event, userTask: TaskModel) {
    console.log(this.selectedItem);
    console.log(this.tasks);
    console.log(userTask);
    for (let i = 0; i < this.tasks.length; i++) {
      if (this.tasks[i].Name === userTask.Name) {
        this.tasks.splice(i, 1);
      }

    }
    event.stopPropagation();

    this.appointment.AppointmentUserTasks.push(
      {
        userTask: userTask,
        startTime: new Date(),
        count: 1,
        state: this._data.taskStates.find(x => x.Code === 'new')
      }
    );

    this.SaveAppointment();
    userTask = undefined;

  }

  deleteService(pservice) {
    this.appointment.AppointmentUserTasks.splice(pservice, 1);
  }


  compareObjects(o1: any, o2: any): boolean {
    return o1.id === o2.id;
  }

  getStateNameByCode(code) {
    var res = '';
    var m = this._data.taskStates.find(x => x.Code === code);
    if (m) {
      res = m.Name;
    }

    return res;
  }

  SaveAppointment() {
    this.appointment.number = this._data.SaveAppointment(this.appointment).number;
  }

  checkData() {
    this.appointment.client && this.appointment.AppointmentUserTasks.length > 0;
  }


  startAppointment() {
    this.appointment.state = this._data.appointmentStaes.find(x => x.Code === 'send');
  }

  verifyClient() {
    this.appointment.state = this._data.appointmentStaes.find(x => x.Code === 'started');
  }

  finishAppointment() {
    this.appointment.state = this._data.appointmentStaes.find(x => x.Code === 'closed');
    this.SaveAppointment();
  }
}



