import {Component, ViewChild, OnInit} from '@angular/core';
import {Inject, Injectable} from '@angular/core';
import {LOCAL_STORAGE, StorageService} from 'ngx-webstorage-service';
import {DataService} from '../services/data.service';
import {AppointmentUserTaskModel, AbstractDictModel, AppointmentModel, ClientModel, TaskModel} from '../models';


@Component({
  selector: 'app-meetings',
  templateUrl: './meetings.component.html',
  styleUrls: ['./meetings.component.scss']
})

export class MeetingsComponent implements OnInit {

  appointments: AppointmentModel[];
  userFilter = {client: {}};

  constructor(private _data: DataService) {

  }

  ngOnInit() {
    this.appointments = this._data.appointmens;
    console.log(this.appointments);
  }

  openMeetting(app: AppointmentModel) {
      this._data.currentAppointmenNumber = app.number;
  }

  CreateMeetting() {
    this._data.currentAppointmenNumber = 0;
  }

}



